  --font-size-h2: 18px;
  --font-size-body: 16px;
  --font-size-15: 15px;
  --font-size-14: 14px;
  --font-size-0: 0;
  /* Font Weights */
  --font-weight-900: 900;
  --font-weight-700: 700;
  --font-weight-600: 600;
  --font-weight-500: 500;
  --font-weight-400: 400;
  /* Border Radius */
