  align-items: center;
}
.zwssgr-popup1 .zwssgr-rating {
  pointer-events: none;
}
.zwssgr-popup1 .zwssgr-final-review-wrap {
  margin: initial;
}
.zwssgr-popup1 .zwssgr-profile-logo {
  padding-right: 0;
}
.zwssgr-popup2 .zwssgr-title-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}
.zwssgr-popup2 .zwssgr-title-wrap h3 {
  font-size: var(--font-size-14);
  font-weight: var(--font-weight-700);
  margin: 0;
  padding-left: 10px;
  font-family: var(--font-primary);
  color: var(--body-color);
}
.zwssgr-popup2 .zwssgr-info-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}
.zwssgr-popup2 .zwssgr-rating {
  padding: 0 10px;
  pointer-events: none;
}
.zwssgr-popup2 .final-rating {
  font-size: var(--font-size-body);
  font-weight: var(--font-weight-700);
  font-family: var(--font-primary);
  color: var(--body-color);
  pointer-events: none;
}

.scrollable-content {
  padding: 50px 30px;
  height: 100%;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #888 #f1f1f1;
  box-sizing: border-box;
}

.zwssgrpopup2 {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}
.zwssgrpopup2 .zwssgr-popup-content {
  position: fixed;
  right: 0;
  top: 0;
  transform: none;
  left: auto;
  width: 40%;
  height: 100vh;
  background: var(--white-color);
  border-radius: var(--border-radius-extra-small);
  box-shadow: -5px 0 15px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  /** If the breakpoint exists in the map. */
  /** Get the breakpoint value. */
  /** Write the media query. */
  /** If the breakpoint doesn't exist in the map. */
}
@media (max-width: 991px) {
  .zwssgrpopup2 .zwssgr-popup-content {
    width: 70%;
  }
}
@media (max-width: 720px) and (orientation: portrait) {
  .zwssgrpopup2 .zwssgr-popup-content {
    max-width: 750px;
    width: 90%;
  }
}
@media (max-width: 992px) and (orientation: landscape) {
  .zwssgrpopup2 .zwssgr-popup-content {
    max-width: 750px;
    width: 90%;
  }
}
.zwssgrpopup2 .zwssgr-list-inner {
  background-color: var(--white-color);
  border: 1px solid var(--white-color-100);
}
.zwssgrpopup2 .zwssgr-list-content-wrap {
  padding-left: 80px;
}